      <ul>
        <li><span>订单号</span><span>{{params.id}}</span></li>
        <li><span>购买价格</span><span>{{params.startprice}}</span></li>
        <li><span>结算价格</span><span>{{params.endprice}}</span></li>
        <li><span>方向</span><span :style="params.state*1?{color:'#58bc58'}:{color:'red'}">{{params.state*1 ? '买跌' : '买涨'}}</span></li>
        <li><span>购买规格</span><span>{{params.time}}秒</span></li>
        <li><span>建仓时间</span><span>{{params.createtime}}</span></li>
