        <li>
          <span>手续费</span>
          <span v-if="params.endstatus*1 === 1">{{(params.spread*1+params.poundage*1).toFixed(2)}}</span>
          <span v-else-if="params.endstatus*1 === 2 || params.endstatus*1 === 3 ? true: false">{{(params.poundage*1).toFixed(2)}}</span>
